*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
html {
    font-size: 62.5%;
}
:root {
    --sm: 1.4rem;
    --md: 1.6rem;
    --lg: 1.8rem;
    --title: 3.2rem;
}
$background-blue: #202124;
$blue-light: #61DAFA;
$just-black: #000000;
$check-black: #434343;
$just-white: #ffffff;
$yellow: #FFCC73;
$green: #1C931A;
$red: #EE3A3A;


body {
    margin: 0;
    padding: 0;
    background-color: $background-blue;
    font-family: 'Times New Roman';
}
#root {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 5rem;
}
@media (min-width: 1024px) {
    #root {
        margin-top: 5rem;
    }
}