@import '../styles/global.scss';

.principal {
    display: flex;
    flex-direction: column;
    width: 100%;
    min-width: 30rem;
    max-width: 50rem;
    align-items: center;
    border-radius: 5rem;
    background-color: $just-white;
    box-shadow: 0.4rem 0.4rem rgba($color: #000000, $alpha: 0.25); 
}
h1{
    margin-top: 5rem;
    text-align: center;
    font-style: normal;
    font-weight: 700;
    font-size: var(--title);
}
.principal > p {
    // margin-left: 100px;
    color: $red;
}
b{
    margin-left: 8px;
}
img{
    display: none;
    width: 225px;
    height: 225px;
    margin: 0 auto;
}
@media (min-width: 1024px) {
    img{
        display: block;
    }
}