@import '../styles/global.scss';

.TodoSearch {
    width: 90%;
    min-width: 30rem;
    max-width: 40rem;
    padding: 0 10px 0 10px;
    height: 4rem;
    border: none;
    border-radius: 10px;
    box-shadow: 0 .4rem .4rem rgba($color: #000000, $alpha: 0.35);
    margin: 1.6rem;
}
::placeholder {
    padding: .8rem;
    font-style: normal;
    font-weight: 400;
    font-size: 1.6rem;
    line-height: 1.8rem;
}
:focus {
    outline-color: $blue-light;
}
.TodoSearch:disabled{
    background-image: 
    linear-gradient(90deg, #ccc 0,
    rgb(229 229 229 / 90%) 4.0rem,
    #ccc 8.0rem);
    background-size: 300%;
    background-position: 100% 0;
    border-radius: 1.0rem;
    animation: shimmer 1.5s infinite;
}
@keyframes shimmer {
    to {
        background-position: -190% 0;
    }
}