@import '../styles/global.scss';

.CreateTodoButton {
    margin-bottom: 5rem;
    width: 10rem;
    height: 4rem;
    background: $yellow;
    border: .1rem solid #000000;
    box-shadow: 0 .4rem .4rem rgba(0, 0, 0, 0.25);
    border-radius: 1rem;
    cursor: pointer;
    font-style: normal;
    font-weight: 400;
    font-size: var(--md);
}