@import '../styles/global.scss';

.NotFound-container {
    margin-top: 100px ;
    width: auto;
    height: 75px;
    text-align: center;
}
.NotFound-container p{
    color: $just-white;
    font-size: 30px;
    margin: 16px;
}
.NotFound-container p:nth-child(1){
    font-size: 100px;
}
.NotFound-container p:nth-child(3){
    font-size: 50px;
}